document.addEventListener('DOMContentLoaded', function () {

    $('.bottom-nav__item span').on('click', function () {
        $(this).parent().next().addClass('active');
        $('.header__bottom').addClass('scroll-off');
    });

    $('.back').on('click', function () {
        $(this).parents('.submenu').removeClass('active');
        $('.header__bottom').removeClass('scroll-off');
    });


    lightGallery(document.getElementById('lightgallery'), {
        plugins: [lgZoom, lgThumbnail],
        speed: 500,
    });


    const castomSelect = document.querySelectorAll('.castom-select');
    castomSelect.forEach(function (item) {
        const choices = new Choices(item);
    });


    function selectFanc() {
        const select = document.querySelectorAll('select');
        const selectParent = document.querySelectorAll('.select-block-wrap');

        if (select) {
            select.forEach(item => {
                item.addEventListener('blur', () => {
                    item.parentElement.classList.remove('active');
                });
            });

            select.forEach(item => {
                item.addEventListener('change', () => {
                    item.classList.add('active');
                });
            });

            select.forEach(item => {
                item.addEventListener('click', () => {
                    if (item.parentElement.classList.contains('active')) {
                        item.parentElement.classList.remove('active');
                    } else {
                        item.parentElement.classList.add('active');
                    }
                });
            });

            window.addEventListener('scroll', function () {
                selectParent.forEach(item => {
                    item.classList.remove('active');
                });
            });
        }
    }

    selectFanc();

});